.wtc-periodPicker-cycle-wrap {
  text-align: center;
  padding: 12px 0;
  color: #666;
  border-bottom: 1px solid #f1f1f1;
}
.wtc-periodPicker-select {
  position: relative;
}
.wtc-periodPicker-att-term .wtc-periodPicker-select-wrap {
  padding: 12px 12px 0 12px;
  display: flex;
}
.wtc-periodPicker-att-term .wtc-periodPicker-input-wrap {
  display: flex;
  flex: 1;
  background-color: #f2f2f2;
  padding: 5px 0;
  border-radius: 4px;
}
.wtc-periodPicker-att-term .wtc-periodPicker-input-wrap .wtc-periodPicker-input-icon {
  flex: 0;
  color: #999;
  padding: 0 10px;
}
.wtc-periodPicker-att-term .wtc-periodPicker-input-wrap .wtc-periodPicker-input {
  flex: 1;
  outline: none;
  border: none;
  background-color: initial;
}
.wtc-periodPicker-input-wrap .wtc-periodPicker-input::placeholder {
  color: #ccc;
}
.wtc-periodPicker-att-term .wtc-periodPicker-select-btn {
  flex: 0 0 30px;
  color: #276FF5;
  text-align: center;
  margin-left: 10px;
  padding: 3px 0;
}
.wtc-periodPicker-att-term .wtc-periodPicker-picker-wrap {
  margin: 12px 0 0 0;
  display: flex;
  height: 330px;
  border-top: 1px solid #f1f1f1;
}
.wtc-periodPicker-picker-wrap .wtc-periodPicker-picker-left {
  flex: 0 0 100px;
  overflow: auto;
  background-color: #FaFaFa;
  border-right: 1px solid #f1f1f1;
  /*margin-right: 10px;*/
}
.wtc-periodPicker-picker-wrap .wtc-periodPicker-picker-left .wtc-periodPicker-picker-left-item {
  padding: 10px 0;
  text-align: center;
  color: #666;
}
.wtc-periodPicker-picker-wrap .wtc-periodPicker-picker-left .wtc-periodPicker-year-checked {
  color: #212121;
  background-color: #fff;
  border-top: 1px solid #f1f1f1;
  border-bottom: 1px solid #f1f1f1;
}
.wtc-periodPicker-picker-wrap .wtc-periodPicker-picker-left .wtc-periodPicker-picker-left-item:first-child {
  border-top: none;
}
.wtc-periodPicker-picker-wrap .wtc-periodPicker-picker-right {
  flex: 1;
  overflow: auto;
  margin-left: 12px;
}
.wtc-periodPicker-picker-wrap .wtc-periodPicker-picker-right .wtc-periodPicker-picker-right-empty {
  text-align: center;
  padding-top: 50px;
}
.wtc-periodPicker-picker-wrap .wtc-periodPicker-picker-right .wtc-periodPicker-picker-right-empty-text {
  color: #999;
}
.wtc-periodPicker-picker-wrap .wtc-periodPicker-picker-right .wtc-periodPicker-picker-right-empty > img {
  width: 70%;
}
.wtc-periodPicker-picker-wrap .wtc-periodPicker-picker-right .wtc-periodPicker-picker-right-item {
  position: relative;
  padding: 10px 24px 10px 0px;
  font-size: 0;
  border-bottom: 1px solid #f1f1f1;
  word-break: break-all;
}
.wtc-periodPicker-picker-right .wtc-periodPicker-picker-right-item .wtc-periodPicker-picker-right-item-match {
  color: #276FF5;
}
.wtc-periodPicker-picker-wrap .wtc-periodPicker-picker-right .wtc-periodPicker-picker-right-item > span {
  font-size: 14px;
}
.wtc-periodPicker-picker-wrap .wtc-periodPicker-picker-right .wtc-periodPicker-period-default {
  /*background-color: #eee;*/
}
.wtc-periodPicker-cycle-dialog {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  overflow: auto;
}
.wtc-periodPicker-cycle-dialog-item {
  position: relative;
  padding: 10px 24px 10px 10px;
  font-size: 0;
  border-bottom: 1px solid #f1f1f1;
  word-break: break-all;
}
.wtc-periodPicker-cycle-dialog-item > span,
.wtc-periodPicker-cycle-dialog-item > i {
  font-size: 14px;
}
.wtc-periodPicker-picker-right-item-checked-icon {
  position: absolute;
  display: none;
  right: 6px;
  top: 50%;
  transform: translateY(-50%);
  color: #276FF5;
}
.wtc-periodPicker-period-default .wtc-periodPicker-picker-right-item-checked-icon {
  display: block;
}
.wtc-periodPicker-cycle-slide-enter-active,
.wtc-periodPicker-cycle-slide-leave-active {
  transition: transform 0.3s;
}
.wtc-periodPicker-cycle-slide-enter,
.wtc-periodPicker-cycle-slide-leave-to {
  transform: translateY(100%);
}
