.wtc-periodPicker-wrap {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  font-size: 14px;
  color: #212121;
  background-color: #fff;
}
.wtc-periodPicker-wrap .wtc-periodPicker-title {
  padding: 12px;
  font-size: 18px;
  font-weight: 700;
  border-bottom: 1px solid #f1f1f1;
}
.wtc-periodPicker-wrap .wtc-periodPicker-close-btn {
  position: absolute;
  right: 12px;
  top: 15px;
  font-size: 16px;
  color: #999;
}
.wtc-periodPicker-slide-bottom-enter-active,
.wtc-periodPicker-slide-bottom-leave-active {
  transition: transform 0.5s;
}
.wtc-periodPicker-slide-bottom-enter,
.wtc-periodPicker-slide-bottom-leave-to {
  transform: translateY(100%);
}
